/**
 * 把list转换为object
 * @param obj 原对象
 * @param lists 待转换的list
 * @param keyName list中key的名称
 * @returns 转换后的objects
 */
export const List2Objects = function (lists, keyName) {
  const tmpObjects = {};
  lists.forEach((item) => {
    const keyValue = item[keyName];
    tmpObjects[keyValue] = item;
  });

  return tmpObjects;
};

