/**
 * 文件上传
 */
export const fileUpload = async (url, data, headers) => {
  return axios({
    url,
    data,
    method: "put",
    headers,
  });
};

/**
 * 文件上传
 * @param {File} fileData 文件
 * @returns Promise
 */
export const uploadFile = function ({ session_id, fileData }) {
  return new Promise((resolve, reject) => {
    let fr = new FileReader();
    fr.readAsBinaryString(fileData);
    fr.addEventListener("loadend", async () => {
      const result = fr.result;
      let digest = md.md5.create().update(result).digest();
      let contentMd5 = window.btoa(digest.data);

      const headers = {
        "Content-MD5": contentMd5,
      };

      const formData = new FormData();
      formData.append("file", fileData);

      let applyResp;
      let downResp;

      try {
        // 申请上传
        applyResp = await imgUploadApply({
          session_id,
          file_type: UPLOAD_FILE_TYPE_ENUM.image,
          filename: fileData.name,
          md5: headers["Content-MD5"],
          contentType: fileData.type,
          size: fileData.size,
        });
        // 如果不存在则上传，并查回调
        if (!applyResp.hit) {
          await fileUpload(applyResp.upload_url, fileData, {
            "Content-MD5": headers["Content-MD5"],
            "Content-Type": fileData.type,
            // "Content-Length": fileData.size,
          });
        }
        downResp = await imgUploadCallback({
          session_id: session_id,
          file_id: applyResp.file_id,
        });
      } catch (error) {
        reject({
          message: error.message || "上传失败，请稍后重试",
        });

        return;
      }
      resolve({ file_id: applyResp.file_id, ...downResp });
    });
  });
};
/**
 * 文件申请下载
 */
export const imgUploadCallback = async (req, headers) => {
  if (!req.session_id || !req.file_id) {
    throw new Error("参数错误");
  }
  let resp;
  try {
    resp = await uploadCallback(req, headers);
  } catch (error) {
    throw new Error(error.msg);
    // return {
    //   download_url: "",
    // };
  }

  return resp;
};
/**
 * 通过参数名获取url中的参数值
 * @param  {String} url url
 * @return {Object}
 */
export const getQueryValue = (url) => {
  // result为存储参数键值的集合
  const result = {};
  //建立正则匹配路径中有没有问号
  var reg = /\?/g;
  if (reg.test(url)) {
    // str为？之后的参数部分字符串
    const str = url.substr(url.indexOf("?") + 1);
    // arr每个元素都是完整的参数键值
    const arr = str.split("&");
    for (let i = 0; i < arr.length; i++) {
      // item的两个元素分别为参数名和参数值
      const item = arr[i].split("=");
      result[item[0]] = item[1];
    }
  }
  return result;
};

/**
 * url临时访问地址转file对象
 * @param { url, filename, type } 临时地址，文件名，MIME类型
 */
export const blobUrlToFile = ({ url, filename, type }) => {
  return new Promise((resolve, reject) => {
    let xhr = new XMLHttpRequest();
    xhr.open("GET", url, true);
    xhr.responseType = "blob";
    xhr.onload = function () {
      if (this.status == 200) {
        let myBlob = this.response;
        let files = new window.File([myBlob], filename, { type: type }); // myBlob.type 自定义文件名
        resolve(files);
      } else {
        reject(false);
      }
    };
    xhr.send();
  });
};

/**
 * file转blob对象
 */
export const fileToBlob = (file) => {
  return new Blob([file], { type: file.type });
};
/**
 * blob转file对象
 */
export const blobToFile = ({ blob, filename, type }) => {
  return new File([blob], filename, { type });
};
/**
 * file转base64
 */
export const fileToBase64 = (file) => {
  return new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.onload = (e) => {
      resolve(e.target.result);
    };
    reader.onerror = () => {
      reject();
    };
    reader.readAsDataURL(file);
  });
};
