<template>
  <div class="refresh">
    <van-pull-refresh
      v-model="isRefreshLoading"
      pulling-text="下拉加载数据"
      loosing-text="释放即可刷新"
      :success-text="isRefreshDisabled ? '无更多数据' : ''"
      :success-duration="800"
      :disabled="isRefreshDisabled"
      @refresh="onRefresh"
    >
      <div ref="courseListHeight">
        <div v-for="(item, index) in videoList" :key="index" class="content">
          {{ item }}
        </div>
      </div>
    </van-pull-refresh>
  </div>
</template>
<script>
import { PullRefresh } from "vant";
export default {
  components: {
    "van-pull-refresh": PullRefresh,
  },
  data() {
    return {
      dataList: [
        111, 112, 113, 114, 115, 116, 117, 118, 119, 120, 121, 122, 123, 124,
        125, 126, 127, 128, 129, 130, 131, 132, 133, 134, 135,
      ],
      params: {
        page_size: 5,
        page_num: 1,
      },
      isRefreshLoading: false, // 是否处于加载中状态
      isRefreshDisabled: false, // 是否禁用下拉刷新
      videoList: [], // 数据内容
      courseListPageHeight: 0, // 页面的高度
      domVantRefreh: "", // 获取van-loading到顶部的高度
      lockOneApiState: false, // 防止反复的请求api
    };
  },
  beforeMount() {
    this.getapi();
  },
  methods: {
    onRefresh() {
      // 获取van-loading到顶部的高度
      this.domVantRefreh = document.querySelector(".van-loading");
      window.addEventListener("scroll", this.loadingPage);
      this.params.page_num++;
      this.getapi();
    },
    getapi() {
      this.getDataList(this.params).then((res) => {
        console.log(res);
        if (res.code == 200) {
          // 第一次添加数据
          if (this.params.page_num == 1) {
            this.videoList = res.result.video_list;
            this.$nextTick(() => {
              window.scrollBy(0, 9999);
              this.courseListPageHeight =
                this.$refs.courseListHeight.clientHeight;
            });
          } else {
            this.videoList.unshift(...res.result.video_list);
          }
          // 对后面的数据进行处理
          if (
            res.result.video_list.length !== 0 &&
            this.params.page_num !== 1
          ) {
            this.isRefreshDisabled = false;
            this.$nextTick(() => {
              let heightElement =
                this.$refs.courseListHeight.clientHeight -
                this.courseListPageHeight;
              this.courseListPageHeight =
                this.$refs.courseListHeight.clientHeight;
              window.scrollBy(0, heightElement);
            });
          }
          // 请求无数据的时候
          if (res.result.video_list.length == 0) {
            this.isRefreshDisabled = true;
            this.isRefreshLoading = false;
            window.removeEventListener("scroll", this.loadingPage);
          }
        }
        this.lockOneApiState = true;
      });
    },
    loadingPage() {
      // 由于我们给与了margin-top: 20px;有20的高度，所以使用 0 恰到好处,能再快看到加载状态的时候，进行了加载
      if (
        this.domVantRefreh.getBoundingClientRect().top >= 0 &&
        this.lockOneApiState == true &&
        this.isRefreshDisabled == false
      ) {
        this.params.page_num++;
        this.getapi();
        this.lockOneApiState = false;
      }
    },
    // 伪装数据请求
    getDataList(params) {
      return new Promise((resolve) => {
        let result = {
          code: 200,
          result: {
            video_list: this.dataList.splice(
              1 * params.page_num - 1,
              params.page_size
            ),
          },
        };
        setTimeout(() => {
          resolve(result);
        }, 1000);
      });
    },
  },
};
</script>
<style lang="scss" scoped>
.refresh {
  padding: 0 20px 20px 20px;
  .content {
    width: 100%;
    height: 320px;
    text-align: center;
    margin-top: 20px;
    background-color: cadetblue;
    font-size: 22px;
  }
}
</style>
